<template>
  <div id="main-container">
    <div class="input-wrapper">
      <input-text
        v-model="selected.figureName"
        name="figureName"
        :placeholder="'이름 혹은 별칭 입력'"
      />
      <button
        class="basic__button"
        @click="_pageInput"
      >
        검색
      </button>
    </div>
    <div class="search-conditions">
      <span
        @click="_checkPaged"
        :class="{ 'checked-search-param': searchSuffix.paged.checked }">
        기본순
      </span>
      <span
        @click="_checkPagedReverse"
        :class="{ 'checked-search-param': searchSuffix.pagedReverse.checked }">
        오래된순
      </span>
      <span
        @click="_checkPagedNames"
        :class="{ 'checked-search-param': searchSuffix.pagedNames.checked }">
        이름순
      </span>
      <span
        @click="_checkPagedDate"
        :class="{ 'checked-search-param': searchSuffix.pagedDate.checked }">
        최신구매순
      </span>
    </div>
    <table class="responsive">
      <caption>
        {{ $route.meta.title }}
        <span class="notice">
          * 기본은 최신 등록순으로 정렬됩니다.
        </span>
      </caption>
      <thead>
      <tr>
        <th scope="col">등급</th>
        <th scope="col">이름</th>
        <th scope="col">구매날짜</th>
        <th scope="col">가격</th>
      </tr>
      </thead>
      <tbody>
      <tr
        v-for="(item, index) in paginationParams.content" :key="index"
        class="hover-tr"
        @click="_routeToGunplaDetail(item)">
        <td data-label="등급">{{ item.grade.value }}</td>
        <td data-label="이름">{{ item.figureName }}</td>
        <td
          v-if="item.date !== null"
          data-label="구매날짜">
          {{ String(item.date).split('T')[0] }}
        </td>
        <td v-else data-label="구매날짜">
          등록되지 않음
        </td>
        <td data-label="가격">
          {{ String(item.price).replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,') }}
        </td>
      </tr>
      </tbody>
    </table>
    <pagination
      v-model="paginationParams.currentPage"
      with-text
      :page-count="paginationParams.totalPages"
      :per-page="paginationParams.numberOfElements"
      @input="_pageInput"
    />
    <div class="input-wrapper">
      <button
        @click="_downloadExcel"
        class="basic__button">
        다운로드
      </button>
      <button
        @click="_routeToGunplaCreate"
        class="basic__button">
        신규등록
      </button>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from 'pinia'
import { figureStore } from '@/store/figures'
import { usersStore } from '@/store/users'
import { useCookies } from 'vue3-cookies'
import pagination from '@/components/global/pagination'
import sweetAlert from '@/wrapper/sweet-alert'
import jwtDecode from 'jwt-decode'

const { cookies } = useCookies()

export default {
  name: 'user-gunpla-list',
  data () {
    return {
      searchSuffix: {
        paged: { checked: true, suffix: 'paged' },
        pagedReverse: { checked: false, suffix: 'paged-reverse' },
        pagedNames: { checked: false, suffix: 'paged-names' },
        pagedDate: { checked: false, suffix: 'paged-date' }
      },
      checkedSuffix: 'paged',
      selected: {
        figureName: ''
      },
      paginationParams: {
        currentPage: 1,
        content: [],
        totalPages: 0,
        number: 0,
        size: 20,
        numberOfElements: 0
      }
    }
  },
  components: { pagination },
  computed: {
    ...mapState(usersStore, ['userCustomInfo', 'loggedInAccessToken']),
    ...mapState(figureStore, ['getFigureList'])
  },
  methods: {
    ...mapActions(figureStore, ['setFigureList', 'setFigureInfo']),
    async _routeToGunplaCreate () {
      const newFigureList = {
        checkedSuffix: this.checkedSuffix,
        selected: this.selected,
        paginationParams: this.paginationParams
      }
      await this.setFigureList(newFigureList)
      await this.$router.push({ name: 'user-gunpla-create' })
    },
    _downloadExcel () {
      this.downloadExcel(this.userCustomInfo.userOid).then(() => {
        sweetAlert.noIcon('엑셀로 다운로드 완료 되었습니다 :)', '확인')
      })
    },
    async _routeToGunplaDetail (item) {
      const newFigureList = {
        checkedSuffix: this.checkedSuffix,
        selected: this.selected,
        paginationParams: this.paginationParams
      }
      await this.setFigureInfo(item)
      await this.setFigureList(newFigureList)
      await this.$router.push({ name: 'user-gunpla-detail' })
    },
    _checkPaged () {
      this.searchSuffix.paged.checked = true
      this.searchSuffix.pagedReverse.checked = false
      this.searchSuffix.pagedNames.checked = false
      this.searchSuffix.pagedDate.checked = false
      this.checkedSuffix = this.searchSuffix.paged.suffix
      this._getPagedFigure()
    },
    _checkPagedReverse () {
      this.searchSuffix.paged.checked = false
      this.searchSuffix.pagedReverse.checked = true
      this.searchSuffix.pagedNames.checked = false
      this.searchSuffix.pagedDate.checked = false
      this.checkedSuffix = this.searchSuffix.pagedReverse.suffix
      this._getPagedFigure()
    },
    _checkPagedNames () {
      this.searchSuffix.paged.checked = false
      this.searchSuffix.pagedReverse.checked = false
      this.searchSuffix.pagedNames.checked = true
      this.searchSuffix.pagedDate.checked = false
      this.checkedSuffix = this.searchSuffix.pagedNames.suffix
      this._getPagedFigure()
    },
    _checkPagedDate () {
      this.searchSuffix.paged.checked = false
      this.searchSuffix.pagedReverse.checked = false
      this.searchSuffix.pagedNames.checked = false
      this.searchSuffix.pagedDate.checked = true
      this.checkedSuffix = this.searchSuffix.pagedDate.suffix
      this._getPagedFigure()
    },
    _revertPagedDate () {
      this.searchSuffix.paged.checked = false
      this.searchSuffix.pagedReverse.checked = false
      this.searchSuffix.pagedNames.checked = false
      this.searchSuffix.pagedDate.checked = true
      this.checkedSuffix = this.searchSuffix.pagedDate.suffix
    },
    _revertPaged () {
      this.searchSuffix.paged.checked = true
      this.searchSuffix.pagedReverse.checked = false
      this.searchSuffix.pagedNames.checked = false
      this.searchSuffix.pagedDate.checked = false
      this.checkedSuffix = this.searchSuffix.paged.suffix
    },
    _revertPagedReverse () {
      this.searchSuffix.paged.checked = false
      this.searchSuffix.pagedReverse.checked = true
      this.searchSuffix.pagedNames.checked = false
      this.searchSuffix.pagedDate.checked = false
      this.checkedSuffix = this.searchSuffix.pagedReverse.suffix
    },
    _revertPagedNames () {
      this.searchSuffix.paged.checked = false
      this.searchSuffix.pagedReverse.checked = false
      this.searchSuffix.pagedNames.checked = true
      this.searchSuffix.pagedDate.checked = false
      this.checkedSuffix = this.searchSuffix.pagedNames.suffix
    },
    _pageInput (page) {
      this._getPagedFigure(page - 1)
    },
    async _getPagedFigure (page) {
      if (this.checkedSuffix === undefined) {
        this.checkedSuffix = 'paged'
      }
      await this.getPagedFigure(
        this.userCustomInfo.userOid,
        this.selected.figureName, page,
        this.paginationParams.size,
        this.checkedSuffix).then(res => {
        if (this.checkedSuffix === 'paged') this._revertPaged()
        if (this.checkedSuffix === 'paged-reverse') this._revertPagedReverse()
        if (this.checkedSuffix === 'paged-names') this._revertPagedNames()
        if (this.checkedSuffix === 'paged-date') this._revertPagedDate()
        this.paginationParams = res
        this.paginationParams.currentPage = this.paginationParams.number + 1
      })
    }
  },
  created () {
    if (this.userCustomInfo === undefined) {
      sweetAlert.noIcon('잘못된 경로로 진입하셨거나 인증 정보가 없습니다. 로그인 화면으로 이동합니다.', '확인')
      this.logout()
    } else {
      if (new Date(jwtDecode(this.loggedInAccessToken).exp * 1000) <= new Date()) {
        sweetAlert.noIcon('자동 로그인 기간이 만료 되었습니다. 다시 로그인 해 주세요 :)', '확인')
        this.logout()
      }
    }
  },
  async mounted () {
    if (this.getFigureList.selected === undefined) {
      await this._getPagedFigure()
    } else {
      this.paginationParams.currentPage = this.getFigureList.paginationParams.currentPage
      this.selected = this.getFigureList.selected
      this.checkedSuffix = this.getFigureList.checkedSuffix
      await this._pageInput(this.paginationParams.currentPage)
      cookies.remove('figureList')
    }
  }
}
</script>
<style scoped lang="scss">
.grid-item {
  height: 160px !important;
}
.button__wrapper {
  margin-top: 30px;
  text-align: right;
}
.checked-search-param {
  font-weight: bold;
}
</style>
